

<script>
import LeadDataService from "../../../services/DataService";
export default {
    props:{
        modalTaskItem:null
    },
     data: function () {
            return {
                isLoading:false,
                id: null,
                duedate: new Date(),
                Subject: '',
                priority: [],
                Message: '',
                priorityValue: '',
                modal: null,
                isView: false,
                popover: {
                visibility: 'click'
            },
                optionsMindate: {
    format: 'MMM dd yyyy',
    useCurrent: false,
    showClear: true,
    showClose: true,
    allowInputToggle: true,
    minDate: new Date(Date.now())
},
            }
        },
        created: function () {
           this.modal = this.modalTaskItem;
            if (this.modal != null) {
                this.FetchTaskViewData(this.modal.followup_notification_id);
                this.isView = true;
            }
            this.GetPriority();
        },
        methods: {
            GetPriority: function () {
            var vm = this;
                var url = `id=&moduleName=${vm.$route.params.moduleName}&type=PRIORITY`;
                 
                 LeadDataService.getLeaOwnerBySearch(url).then(function (response) {
                   var json = response.data;
                     
                    if (json != null) {
                        vm.priority = json.DATA;
                        return vm.priority;
                    }
                     
                });
            },
            getSelectedValue: function (event) {
                this.priorityValue = event.target.value;
            },
            formatDate: function (value) {
                if (value) {
                    return moment(String(value), "YYYY-MM-DD").format(String(Globalize.culture().calendar.patterns.d).toUpperCase())
                }
            },
            formatDateTime: function (value) {
                if (value) {
                    try {
                        var timeFormat = Globalize.culture().calendar.patterns.t;
                        timeFormat = String(timeFormat).replace("t", "A").replace("t", "");
                        var getDate = new Date(moment(String(value)).year(), moment(String(value)).month(), moment(String(value)).date(), moment(String(value)).hours(), moment(String(value)).minutes());
                        return moment(getDate, "YYYY-MM-DD HH:mm:ss").format(String(Globalize.culture().calendar.patterns.d).toUpperCase() + " " + String(timeFormat));
                    } catch (e) {
                        var timeFormat = Globalize.culture().calendar.patterns.t;
                        timeFormat = String(timeFormat).replace("t", "a").replace("t", "");
                        return moment(String(value)).format(String(Globalize.culture().calendar.patterns.d).toUpperCase() + " " + String(timeFormat));
                    }
                }
            },
            closePopup: function () {
            this.$emit('close');
        },
            saveTask: function () {
                var vm = this;             
                this.$refs.form.validate().then((result) =>{
                 if (result) {
                    this.isLoading=true;
                    
                        var data = {
                            sourceId:vm.$route.params.id,
                            moduleName: vm.$route.params.moduleName,
                            subModuleCode: vm.$route.params.subModuleCode,
                            subjecttext: vm.Subject,
                            dueDate: vm.duedate,
                            priorityLavel: vm.priorityValue,
                            messagetext: vm.Message,
                            activityId: vm.modal == null ? 0 : vm.modal.followup_notification_id,
                            activityType: "Task"
                        };
                        
                        var expDate = vm.$parent.expirydate;
                        if (expDate != null) {
                            var newExp = new Date(expDate)
                            var DueDate = new Date(vm.duedate);
                            if (DueDate > newExp) {
                               
                                vm.ShowAlert(vm.$t("Schedule date can not be greater than expected close date."), "warning",true,vm.$t('Alert'));
                                vm.duedate = "";
                                return false;
                            }
                        }
                     
                        var objectPostString = JSON.stringify(data);
                       LeadDataService.Addtask(objectPostString).then(function (response) {
                        vm.isLoading=false; 
                            if (response.data[0].Status == "Success") {
                                vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('Task')]), "success", true, vm.$t("Alert"));
                                vm.duedate = "";
                                vm.Subject = "";
                                vm.priority = "";
                                vm.Message = "";
                                vm.showAddTaskModal = false;
                                vm.$emit('close');
                                vm.$parent.loadData('activities');
                            }
                            else{
                                vm.ShowAlert(vm.$t('AlreadyExists', [vm.$t('Task')]), "warning", true, vm.$t("Alert"));
                            }
                            
                        });
                }
            });
        },
            FetchTaskViewData: function (id) {
                var vm = this;
                vm.optionsMindate.minDate = false;
                var url = `id=${id}&refId=${vm.$route.params.id}&subModuleCode=${vm.$route.params.subModuleCode}&moduleCode=${vm.$route.params.moduleName}`;
                LeadDataService.updateActivityData(url).then(function (response) {
                     
                    if (response.data != null) {
                        var data = response.data.data;
                        vm.duedate =data[0].followUpDate;
                        vm.Message = data[0].followup_notification_message;
                        vm.Subject = data[0].followup_notification_subject;
                        vm.priorityValue = data[0].priority_lavel;
                        var frmFields={}
                    
                     frmFields['Message']=vm.Message;
                     frmFields['Subject']=vm.Subject;
                     frmFields['priorityValue']=vm.priorityValue;
                  
                    // vm.$refs.form.resetForm({values:frmFields});
                    }
                });
            }
        }
   
    }
</script>

<template>
   <div class="modal d-block my-popups dialog">
            <loader :is-visible="isLoading"></loader>
            <div class="modal-dialog">
                <div class="modal-content">
                    <div class="modal-header">
                        <slot name="header">
                           {{$t('AddTask')}}
                        </slot>
                         <button v-on:click="closePopup()" type="button" class="close"><em class="fa fa-times" aria-hidden="true"></em></button>
                    </div>
                    <div class="modal-body">
                        <div class="row form-head" style="display:none;">
                            <div class="col-12">
                                <div class="col-sm-12 col-md-6 col-lg-4 col-xl-4 float-right p-0">
                                    <div class="userguide-box userguide_curve">
                                        <a data-toggle="collapse" href="#user-guidepriority" aria-expanded="false" aria-controls="search-content" class="userguide-btn collapsed"><em class="fa"></em><span>@Resources.UserGuide</span></a>
                                        <div class="clearfix"></div>
                                        <div id="user-guidepriority" class="user-guide collapse">
                                             {{$t('Filltheformtoaddtask')}} <br><br>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <Form ref="form">
                            <div class="d-flex flex-wrap">
                                <div class="col-md-12 p-0">
                                    <div class="row">
                                        <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left">
                                            <div class="form-group">
                                                <label> {{$t("Task")}} {{$t("Subject")}}:<span class="text-danger">*</span></label>
                                                <Field name="Subject" :rules="{ 'required': true }" label="Subject"
                                                    v-slot="{errors,touched,invalid }">
                                                    <input 
                                                        v-bind:class="{ 'form-control': true, 'is-invalid': touched && invalid }"
                                                        id="Subject" name="Subject" maxlength="200" v-model="Subject"
                                                        type="text" />
                                                    <span   name="Subject"
                                                        class="invalid-feedback d-block">{{ errors[0] }}</span>
                                                </Field>
                                               
                                            </div>
                                        </div>
                                        <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left">
                                            <div class="form-group">
                                                <label>{{$t('Task')}} {{$t('ScheduleDate')}}:</label>
                                                <div class="input-group">
                                                   
                                            <v-date-picker  mode="dateTime" :minute-increment="5" class="input-group" v-model="duedate" :min-date="getMinDate()">
                                                            <template v-slot="{ inputValue, inputEvents,togglePopover }">
                                                                <input class="form-control"  :value="inputValue" v-on="inputEvents" />
                                                                <div @click="togglePopover()" class="input-group-append"><span class="input-group-text"><em class="fa fa-calendar"></em></span></div>
                                                            </template>
                                                        </v-date-picker>
                                                   
                                                </div>
                                            </div>
                                        </div>
                                        <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left">
                                            <div class="form-group">
                                                <label>{{$t('Priority')}}:</label>
                                                <Field name="priorityValue" :rules="{'required':false}" label="priorityValue" >
                                                <select id=""  v-on:change="getSelectedValue" class="form-control" v-model="priorityValue">
                                                    <option value="">{{$t('SelectPriorityLevel')}}</option>
                                                    <option v-for="(prty,index) in priority" v-bind:value="prty.value" :key="index"> 
                                                        {{prty.name}}
                                                    </option>
                                                </select>
                                            </Field>
                                            </div>
                                        </div>
                                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left">
                                            <div class="form-group">
                                                <label>{{$t('Message')}}:<span class="text-danger">*</span></label>
                                                <Field name="Message" :rules="{ 'required': true }" label="Message"
                                                    v-slot="{ errors,touched,invalid }">
                                                    <textarea v-model="Message"
                                                        v-bind:class="{ 'form-control': true, 'is-invalid': touched && invalid }"
                                                        maxlength="1000" name="Message"  rows="5"></textarea>
                                                    <span   name="Message"
                                                        class="invalid-feedback d-block">{{ errors[0] }}</span>
                                                </Field>                                               
                                            </div>
                                        </div>
                                    </div>
                                    <div class="row flex-row-reverse">
                                        <div class="col-lg-6 text-right">
                                            <a href="javascript:;" class="btn btn-success save-btn mr-1" v-on:click="saveTask()"><em class="fa fa-save pr-2"></em>{{$t('Save')}}</a>
                                            <a v-on:click="closePopup()" class="btn btn-danger delete-btn"><em class="fa fa-close pr-2"></em>{{$t('Cancel')}}</a>
                                        </div>
                                        <div class="col-lg-6">
                                            <div class="py-2">
                                                <span class="mandatory"> {{$t('MandatoryString')}} </span>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    
</template>